
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

input {
  display: block;
  width: 250px;
  height: 40px;
  line-height: 40px;
  margin: 10px auto;
  padding: 10px;
  border: 1px solid #e3e5e5;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
button {
  display: block;
  width: 250px;
  margin: 0px auto;
  height: 40px;
  line-height: 40px;
  background-color: #42b983;
  border: 1px solid #e3e5e5;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;
}
p {
  cursor: pointer;
}

